body {
  font-family: "Arial", sans-serif;
  background-color: #f0f8ff;
  color: #333;
  margin: 0;
  padding: 20px;
}

h1 {
  text-align: center;
  color: #4a90e2;
}

.game-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
}

.question {
  font-size: 24px;
  margin: 20px 0;
}

.input-answer {
  padding: 10px;
  font-size: 18px;
  width: 100px;
  text-align: center;
}

.submit-button {
  padding: 10px 20px;
  font-size: 18px;
  background-color: #4caf50;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.submit-button:hover {
  background-color: #45a049;
}

.score {
  font-size: 2em;
  margin-top: 20px;
}

.message {
  font-size: 18px;
  color: #e74c3c;
  margin-top: 10px;
}

.app-container {
  text-align: center;
  font-family: Arial, sans-serif;
  padding: 20px;
}

.game {
  margin-top: 20px;
}

.game p {
  font-size: 2em;
}

.answers {
  display: flex;
  justify-content: center;
  gap: 20px;
  flex-wrap: wrap;
}

.answers button {
  padding: 20px 40px;
  font-size: 1.5em;
  border: none;
  border-radius: 10px;
  background-color: #4caf50;
  color: white;
  cursor: pointer;
  transition: background-color 0.3s;
}

.answers button:hover {
  background-color: #45a049;
}

button {
  font-size: 1.5em;
  padding: 10px 20px;
  border: none;
  border-radius: 10px;
  background-color: #008cba;
  color: white;
  cursor: pointer;
  transition: background-color 0.3s;
  margin-top: 1.5em;
}

button:hover {
  background-color: #007bb5;
}

label {
  font-size: 1.5em;
}

select {
  font-size: 1.5em;
  padding: 5px;
  margin-left: 10px;
}
